<template>
  <div class="index">
    <!-- 回到顶部 -->
    <back-top></back-top>
    <el-header class="header">
      <!-- 左侧logo -->
      <h1 :class="['logo', !isCollapse ? 'logo_jpg' : 'logo_png']">安徽省标准化信息服务平台</h1>
      <!-- 退出按钮 -->
      <div class="admin_info">您好，
        <span class="account_name">{{ userInfo.userName }} </span>
        <span class="account_off" @click.stop="accountOff"> 退出</span>
      </div>
    </el-header>
    <el-container class="container">
      <!-- 左侧菜单 -->
      <el-aside :class="[isCollapse ? 'active_path' : '']">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!-- 侧边栏菜单区域 -->
        <el-menu
          :default-active="$route.path"
          background-color="#333"
          text-color="#fff"
          active-text-color="#1E71B1"
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          @open="handleOpen"
          :unique-opened="false"
          :default-openeds="subTitle"
        >
          <!-- <el-menu :default-active="$route.path" background-color="#333" text-color="#eee" :collapse="isCollapse" :collapse-transition="false" unique-opened router @open="handleOpen"> -->
          <!-- 一级菜单 没有子节点-->
          <div v-for="(item, idx1) in menuList" :key="idx1">
            <el-menu-item :index="item.path" v-if="!item.childNode">
                <!-- <i :class="'iconfont icon-' + item.icon"></i> -->
              <img class="iconfont" :src="item.icon"></img>
              <span slot="title">{{ item.name }}</span>
            </el-menu-item>
            <!-- 一级菜单 有子节点的 -->
            <el-submenu :index="item.path" v-if="item.childNode" :class="[item.path === activeMenu ? 'aaa' : '']">
              <!-- 一级模板 -->
              <template slot="title">
                <b :class="item.path === activeMenu ? 'activeBorder' : ''"></b>
                <!-- 一级图标 -->
                <!-- <i :class="'iconfont icon-' + item.icon"></i> -->
              <img class="iconfont" :src="item.icon"></img>
                <!-- 一级文字 -->
                <span v-show="!isCollapse">{{ item.name }}</span>
              </template>
              <!-- 二级菜单 -->
              <el-menu-item :index="subItem.path" v-for="(subItem, idx2) in item.childNode" :key="idx2" @click="saveNavState(item.path, subItem.path)">
                <!-- 二级模板 -->
                <template slot="title">
                  <!-- 二级文字 -->
                  <span :style="{ color: '/' + $route.path.split('/')[1] === subItem.path || $route.path === subItem.path ? '#1E71B1' : '#999' }">{{ subItem.name }}</span>
                </template>
              </el-menu-item>
            </el-submenu>
          </div>
        </el-menu>
      </el-aside>
      <!-- 右侧内容主题 -->
      <el-main>
        <!-- 路由占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import BackTop from '../components/back-top/BackTop'
import menuList from '../utils/menu'
export default {
  components: { BackTop },
  name: 'Index',
  data() {
    return {
      muenuId: '',
      // 左侧菜单数据
      menuList: [],
      // 是否折叠
      isCollapse: false,
      // 被激活的链接地址
      activePath: '',
      // 被激活的一级菜单地址
      activeMenu: '/sendGood',
      showStore: false,
      dialogVisible: false,
      userInfo: {},
      storeList: [],
      curIndex: 0,
      cityCode: '',
      cityStores: [],
      storeName: '全部店铺',
      queryForm: {
        storeName: '',
        total: 0,
        page: 1,
        size: 10
      },
      subTitle: []
    }
  },

  computed: {},

  methods: {
    accountOff() {
      this.httpPost('/user/logout', {}).then(res => {
        // console.log(res)
      })
      this.$sessionData('remove', 'USER')
      this.$router.replace('/login')
    },

    // 一级菜单打开获取url
    handleOpen(key, keyPath) {
      this.activeMenu = key
    },

    // 保存链接的激活状态
    saveNavState(itemPath, subItemPath) {
      // console.log(this.$route.path)
      // console.log(itemPath, subItemPath)
      this.$sessionData('set', 'activeMenu', itemPath)
      this.$sessionData('set', 'activePath', subItemPath)
      this.activeMenu = itemPath
      this.activePath = subItemPath
    },

    // 点击按钮，切换菜单的折叠与展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    }
  },

  mounted() {
      const userInfo = this.$getUser()
      if (userInfo) {
        this.userInfo = userInfo
      }
      console.log(userInfo)
    this.menuList = menuList
    if (this.$sessionData('get', 'activeMenu')) {
      this.activeMenu = this.$sessionData('get', 'activeMenu')
    }
    this.activePath = this.$sessionData('get', 'activePath')
  }
}
</script>
